import logo from "./logo.svg";
import "./App.css";
import Select from "react-select";
import { useEffect, useState } from "react";
import Modal from "react-modal";
import YouTube from "react-youtube";

function App() {
  const [showForm, setShowForm] = useState(true);
  const [selectedOption2, setSelectedOption2] = useState("");
  const [modalIsOpen, setIsOpen] = useState(false);
  const [modalIsOpen2, setIsOpen2] = useState(false);
  const [loading, setLoading] = useState({ status: false, text: "Loading..." });
  const [check, setCheck] = useState("");
  const [formInfo, setFormInfo] = useState({
    name: "",
    number: "",
    tgName: "",
    email: "",
  });
  const api = new XMLHttpRequest();
  api.onreadystatechange = function () {
    if (api.readyState == 4 && api.status == 200) {
      setLoading({
        ...loading,
        status: true,
        text: "Xabaringiz muvaffaqiyatli yuborildi.Tez orada siz bilan bog'lanamiz",
      });
      setFormInfo({ ...formInfo, name: "", number: "", tgName: "", email: "" });
      sessionStorage.setItem("yuborilgan", true);
    } else if (api.readyState == 4 && api.status < 400) {
      setLoading({
        ...loading,
        status: true,
        text: "Xatolik sadir bo`ldi Iltimos qayta urinib ko'ring",
      });
      setFormInfo({ ...formInfo, name: "", number: "", tgName: "", email: "" });
    }
  };
  const customStyles = {
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
      background: "#191919",
    },
  };
  const customSelectStyles = {
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      // const color = chroma(data.color);
      // console.log({ data, isDisabled, isFocused, isSelected });
      return {
        ...styles,
        backgroundColor: isFocused ? "#000" : null,
        // color: "red",
      };
    },
    control: (base, state, isFocused) => ({
      ...base,
      borderRadius: state.isFocused ? "3px 3px 0 0" : 3,
      boxShadow: state.isFocused ? null : null,
      "&:hover": {
        menuList: (base) => ({
          ...base,
          padding: 0,
        }),
      },
    }),
    menu: (base) => ({
      ...base,
      borderRadius: 0,
      marginTop: "10px",
    }),
    menuList: (base) => ({
      ...base,
      padding: 0,
      background: "#333",
      color: "#fff",
      borderRadius: "5px",
    }),
  };
  const [option2, setOption2] = useState([
    { value: "bilmayman", label: "Bilmayman" },
    { value: "beginner", label: "Beginner" },
    { value: "elementary", label: "Elementary" },
    { value: "pre_intermediate", label: "Pre-Intermediate" },
    { value: "Intermediate", label: "Intermediate" },
    { value: "Pre-IELTS", label: "Pre-IELTS" },
    { value: "IELTS", label: "IELTS" },
  ]);

  function openModal() {
    setIsOpen(true);
  }
  function closeModal(a) {
    if (a == 1) {
      setIsOpen(false);
    } else if (a == 2) {
      setIsOpen2(false);
    } else if (a == 3) {
      setLoading({ ...loading, status: false, text: "Loading..." });
    }
  }
  const numberSetter = (number) => {
    if (!number) return setFormInfo({ ...formInfo, number: number });
    const phoneNumber = number.replace(/[^\d]/g, "");
    const phoneNumberLength = phoneNumber.length;
    if (phoneNumberLength < 3) {
      return setFormInfo({ ...formInfo, number: phoneNumber });
    }
    if (phoneNumberLength < 6) {
      let newStr = `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(2)}`;
      return setFormInfo({ ...formInfo, number: newStr });
    }
    if (phoneNumberLength < 8) {
      let newStr2 = `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(
        2,
        5
      )}-${phoneNumber.slice(5, 7)}`;
      return setFormInfo({ ...formInfo, number: newStr2 });
    }
    let newStr3 = `(${phoneNumber.slice(0, 2)}) ${phoneNumber.slice(
      2,
      5
    )}-${phoneNumber.slice(5, 7)}-${phoneNumber.slice(7, 9)}`;
    return setFormInfo({ ...formInfo, number: newStr3 });
  };
  function getWindowSize() {
    const { innerWidth, innerHeight } = window;
    return { innerWidth, innerHeight };
  }
  const checkSent = () => {
    let g = sessionStorage.getItem("yuborilgan");
    if (g) {
      setShowForm(false);
    }
  };
  useEffect(() => {
    window.addEventListener("online", () => {
      alert("Siz yana onlaynsiz ");
    });
    window.addEventListener("offline", () => {
      alert(
        "Siz offline xolatidasiz yo`ki internetingiz juda past,bunday xolatda so`rovlarni yuborib bo`lmaydi"
      );
    });
    function handleWindowResize() {
      setWindowSize(getWindowSize());
    }

    window.addEventListener("resize", handleWindowResize);
    checkSent();
    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);
  const [windowSize, setWindowSize] = useState(getWindowSize());
  const videoOptions = {
    width: "100%",
    height: windowSize.innerWidth > 600 ? "500" : "320",
    playerVars: {
      autoplay: 1,
      controls: 0,
      rel: 0,
      showinfo: 0,
      mute: 0,
      loop: 1,
    },
  };
  const checkandsend = () => {
    if (formInfo.name.trim() != "" && selectedOption2 != "") {
      if (check && formInfo.tgName.trim() != "") {
        setLoading({ ...loading, status: true, text: "Loading..." });
        let text = `Ism Familiyasi: ${formInfo.name} || Telegram Nomi: ${
          formInfo.tgName.startsWith("http")
            ? formInfo.tgName
            : formInfo.tgName.startsWith("@")
            ? formInfo.tgName
            : "@" + formInfo.tgName
        } || Tanlagan darajasi: ${selectedOption2} `;
        let chatId = -1001690943658;
        const telegramUrl = `https://api.telegram.org/bot2115418987:AAFUL3EeFflnOwhu3R07QnkZsixfmxdXF7k/sendMessage?chat_id=${chatId}&text=${text}`;
        api.open("GET", telegramUrl, true);
        api.send();
      } else if (!check && formInfo.number.length == 14) {
        setLoading({ ...loading, status: true, text: "Loading..." });
        let text = `Ism Familiyasi: ${formInfo.name} || Telefon raqami: ${formInfo.number} ||  Tanlagan darajasi: ${selectedOption2}`;
        let chatId = -1001690943658;
        const telegramUrl = `https://api.telegram.org/bot2115418987:AAFUL3EeFflnOwhu3R07QnkZsixfmxdXF7k/sendMessage?chat_id=${chatId}&text=${text}`;
        api.open("GET", telegramUrl, true);
        api.send();
      } else {
        setIsOpen(true);
      }
    } else {
      setIsOpen(true);
    }
  };
  return (
    <div className="wrapper">
      <p
        style={{
          borderBottom: "1px solid rgba(0,0,0,0.1)",
          paddingBottom: "5px",
        }}
      >
        Leap English kurslaridan ro'yxatdan o'tish
      </p>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={() => closeModal(1)}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <p className="alert__p"> iltimos barcha qismlarni to`ldiring</p>
      </Modal>

      {modalIsOpen2 && (
        <div className="custom-modal" onClick={() => closeModal(2)}>
          <div className="yt-modal">
            <p
              className="alert__p"
              style={{ background: "#fff", color: "#000" }}
            >
              Video mavjud emas
            </p>
          </div>
        </div>
      )}

      {loading.status && (
        <div className="custom-modal">
          <div style={{ width: "90%", maxWidth: "350px" }}>
            <p
              className="alert__p"
              style={{ background: "#fff", color: "#000", borderRadius: 5 }}
            >
              {loading.text}
            </p>
            <button
              onClick={() => {
                closeModal(3);
                checkSent();
              }}
              style={{
                height: "40px",
                width: "100%",
                borderRadius: 5,
                marginTop: 10,
                background: "Crimson",
                color: "#fff",
                fontSize: "18px",
                border: "none",
              }}
              disabled={loading.text == "Loading..." ? true : false}
            >
              Yopish
            </button>
          </div>
        </div>
      )}

      {showForm ? (
        <>
          <div className="input-wrapper">
            <p>Ism Familiyangiz</p>
            <input
              name="name"
              className="input-wrapper__input"
              spellCheck={false}
              type="text"
              value={formInfo.name}
              onChange={(e) => {
                /^[a-zA-Z '`]*$/.test(
                  e.target.value
                ) || e.target.value.length == 0
                  ? setFormInfo({ ...formInfo, name: e.target.value })
                  : alert("Faqat harf kirita olasiz");
              }}
              placeholder="Ism familiya"
            />
          </div>
          <div className="input-wrapper">
            <p>Darajangizni tanlang</p>
            <Select
              isSearchable={false}
              options={option2}
              styles={customSelectStyles}
              value={option2.filter(function (options) {
                return options.value === selectedOption2;
              })}
              onChange={(e) => {
                setSelectedOption2(e.value);
              }}
            />
          </div>
          <div className="input-wrapper">
            {!check ? (
              <>
                <p>Telefon raqamingiz </p>
                <input
                  placeholder="+998 ni qo`shmang"
                  className="input-wrapper__input"
                  type="text"
                  value={formInfo.number}
                  onChange={(e) => {
                    numberSetter(e.target.value);
                  }}
                />
              </>
            ) : (
              <>
                <p>Telegramdagi nomingiz </p>
                <input
                  maxLength={32}
                  className="input-wrapper__input"
                  type="text"
                  placeholder="@username"
                  value={formInfo.tgName}
                  onChange={(e) => {
                    /^[a-zA-Z@_0-9`]*$/.test(e.target.value) ||
                    e.target.value.length == 0
                      ? setFormInfo({ ...formInfo, tgName: e.target.value })
                      : console.log("");
                  }}
                />
              </>
            )}

            <div className="checkbox-wrapper">
              <input
                type="checkbox"
                name=""
                id="check"
                onChange={(e) => {
                  setCheck(e.target.checked);
                }}
              />
              <label htmlFor="check">chet eldanman</label>
            </div>
          </div>

          <button
            className="btn"
            onClick={() => {
              checkandsend();
            }}
          >
            Kursga yozilish
          </button>
          <p className="techCenter">
            Texnik nosozlik bo'yicha
            <a href="https://t.me/muffix"> Murojat </a>
          </p>
        </>
      ) : (
        <>
          <p
            style={{
              width: "100%",
              marginTop: "20px",
              fontSize: "17px",
              background: "seagreen",
              padding: "5px",
              borderRadius: "5px",
              color: "#fff",
              textAlign: "center",
            }}
          >
            Xabar muvaffaqiyatli yuborilgan, Yana xabar yubormoqchi bo'lsangiz
            birozdan so'ng urinib ko'ring
          </p>
          <p className="techCenter">
            Texnik nosozlik bo'yicha
            <a href="https://t.me/muffix"> Murojat </a>
          </p>
        </>
      )}
    </div>
  );
}

export default App;
